.MainPageFindDoctor__title {
	font-weight: 700;
	font-size: 32px;
	line-height: 130%;
	color: white;
	text-align: center;
	margin-bottom: 20px;
}

.MainPageFindDoctor {
	margin-left: auto;
	margin-right: auto;
	border-radius: 25px;
	width: 90vw;
	margin-top: 20px;
	padding: 20px;
	background: var(--main-gradient);
}

.MainPageFindDoctor__wrapperShowDoctor {
	margin-top: 10px;
	display: flex;
	gap: 10px;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}

.MainPageFindDoctor__wrapperShow {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 50%;
}

.MainPageFindDoctor__wrapperPrice {
	font-size: 18px;
	display: flex;
	gap: 10px;
	justify-content: space-around;
}

.MainPageFindDoctor__titlePrice {
	font-size: 20px;
	width: 100%;
	text-align: center;
}

.MainPageFindDoctor__notData {
	color: white;
	width: 100%;
	text-align: center;
	font-size: 20px;
	margin-top: 20px;
	margin-bottom: 20px;

}

.MainPageFindDoctor__leftBlock {
	color: white;
	font-size: 24px;
	width: 40%;
	display: flex;
	align-items: center;
	flex-direction: column;
	gap: 20px;
}

.MainPageFindDoctor__dropDounWrapper {
	gap: 30px;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.MainPageFindDoctor__wrapperSerch {
	padding: 0px 50px;
	width: 100%;
	position: relative;
	gap: 20px;
	display: flex;
	justify-content: space-between;
}

@media (max-width: 1000px) {
	.MainPageFindDoctor__wrapperSerch {
		align-items: center;
		flex-direction: column;
	}

	.MainPageFindDoctor__leftBlock {
		width: 80%;
	}
}

@media (max-width: 650px) {
	.MainPageFindDoctor {
		margin-top: 30px;
		width: 99vw;
		padding: 10px;
	}

	.MainPageFindDoctor__wrapperSerch {
		padding: 0px;
	}

	.MainPageFindDoctor__titlePrice {
		font-size: 16px;
	}

	.MainPageFindDoctor__title {
		font-size: 25px;
	}

	.MainPageFindDoctor__leftBlock {
		font-size: 16px;
	}
}

@media (max-width: 450px) {
	.MainPageFindDoctor__leftBlock {
		width: 100%;
	}

	.MainPageFindDoctor__wrapperShowDoctor {
		overflow-x: scroll;
		padding-left: 150px;
		width: 100vw;
	}
}